@import '../variables';

$sky-800: #075985;

header {
  display: inline-block;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  height: 100vh;
  width: 5rem;
  // padding-top: 2rem;
}

.header-link {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;

  &:first-child {
    margin-top: 2rem;
  }
}

.header-icon {
  clip-path: circle(70%);
  width: 2.2rem;
  height: 2.2rem;
  margin: 0 auto;
  fill: $slate-600;

  &:hover {
    background-color: $slate-200;
    outline: $slate-200 solid 8px;
  }
}

.selected-icon {
  background-color: $logo-color;
  outline: $logo-color solid 8px;
  fill: white;

  &:hover {
    background-color: $logo-color;
    outline: $logo-color solid 8px;
    fill: white;
  }
}